import { SalesOrderState, StateAction, StateActionEnum } from '@sales/goliath/data-access';
import { InternetCheckoutFacade } from '../../facade/internet-checkout.facade';
import { forkJoin, mergeMap, pipe } from 'rxjs';
import { map } from 'rxjs/operators';

export const loadCustomerProducts: StateAction<InternetCheckoutFacade> = {
  name: StateActionEnum.EMPTY,
  weight: 0,
  description: 'Load customer products',
  executor: (internetCheckoutFacade: InternetCheckoutFacade) =>
    pipe(
      mergeMap((salesOrder) => {
        return forkJoin([
          internetCheckoutFacade.getCustomerProducts(),
          internetCheckoutFacade.getCustomerAccount(),
        ]).pipe(
          map(([customerProducts, customerAccount]) => {
            internetCheckoutFacade.setCustomerProducts(customerProducts);
            internetCheckoutFacade.setCustomerAccount(customerAccount);
          }),
          map(() => salesOrder)
        );
      })
    ),
  afterExecutionHooks: [],
  stateProduced: SalesOrderState.INITIALIZED,
};
