import { Injectable } from '@angular/core';
import { select, Store } from '@ngrx/store';
import { InstallationAddressInterface, setInstallationAddresses } from '@sales/address/data-access';
import { Observable, of } from 'rxjs';
import { EventInterface, selectEvent } from '@sales/events/data-access';
import { filter } from 'rxjs/operators';
import {
  CustomerAccount,
  CustomerData,
  customerDataState,
  CustomerProduct,
  salesOfferOmapiProductsState,
  salesOrderState,
  SdataSalesOffer,
  setCustomerAccount,
  setCustomerProducts,
  setOmapiProducts,
  setSalesOffer,
  setViewState,
  ViewState,
  viewState,
} from '@sales/internet-checkout/data-access';
import {
  Flow,
  SalesOrderState,
  setCurrentState,
  setFlows,
  setStateActions,
  setSteps,
  StateAction,
  StateActionEnum,
  stateActions,
  Step,
  steps,
} from '@sales/goliath/data-access';
import { InternetCheckoutService } from '../services/internet-checkout.service';
import { ReverseProxyService } from '../services/reverse-proxy.service';
import { SalesOrderUtil } from '../utils/sales-order.util';
import { AddressFacade, AddressUnit } from '@sales/shared/address/data-access';
import { OmapiProduct, OmapiProductService } from '@telenet/ng-lib-omapi';
import { LanguageEnum, LanguageService } from '@telenet/ng-lib-page';

@Injectable({ providedIn: 'root' })
export class InternetCheckoutFacade {
  goliathSteps$ = this.store.select<Step[]>(steps);
  goliathStateActions$ = this.store.select<StateAction<InternetCheckoutService>[]>(stateActions);

  stepEvent$: Observable<EventInterface | undefined> = this.store.pipe(
    select(selectEvent),
    filter((val) => val !== undefined)
  );
  salesOrderState$ = this.store.select<SalesOrderState>(salesOrderState);
  salesOrder$ = of({
    empty: true,
  });
  customerData$ = this.store.select<CustomerData | undefined>(customerDataState);
  salesOfferOmapiProducts$ = this.store.select<OmapiProduct[] | undefined>(salesOfferOmapiProductsState);

  viewState$ = this.store.select<ViewState>(viewState);

  language: LanguageEnum;

  constructor(
    private readonly store: Store,
    private readonly reverseProxyService: ReverseProxyService,
    private readonly addressFacade: AddressFacade,
    private readonly omapiProductService: OmapiProductService,
    private readonly languageService: LanguageService
  ) {
    this.language = this.languageService.getLanguage();
  }

  setInstallationAddresses(addresses: InstallationAddressInterface[]) {
    this.store.dispatch(setInstallationAddresses({ addresses: addresses }));
  }

  getCustomerProducts(): Observable<CustomerProduct[]> {
    return this.reverseProxyService.getCustomerProducts().pipe(SalesOrderUtil.handleNcErrors());
  }

  getOmapiProduct(productId: string): Observable<OmapiProduct> {
    return this.omapiProductService.getProductById('internet-checkout', productId);
  }

  getCustomerAccount(): Observable<CustomerAccount> {
    return this.reverseProxyService.getCustomerAccount().pipe(SalesOrderUtil.handleNcErrors());
  }

  setCustomerProducts(customerProducts: CustomerProduct[]) {
    this.store.dispatch(setCustomerProducts({ customerProducts: customerProducts }));
  }

  setCustomerAccount(customerAccount: CustomerAccount) {
    this.store.dispatch(setCustomerAccount({ customerAccount: customerAccount }));
  }

  setViewState(viewState: ViewState) {
    this.store.dispatch(setViewState({ viewState: viewState }));
  }

  setFlows<T>(flows: Flow<T>[]): void {
    this.store.dispatch(setFlows({ flows: flows as Flow<unknown>[] }));
  }

  setSteps(steps: Step[]): void {
    this.store.dispatch(setSteps({ steps: steps }));
  }

  setStateActions<T>(stateActions: StateAction<T>[]): void {
    this.store.dispatch(setStateActions({ stateActions: stateActions as StateAction<unknown>[] }));
  }

  setCurrentState(state: StateActionEnum): void {
    this.store.dispatch(setCurrentState({ state: state }));
  }

  getAddressById(addressId: string): Observable<AddressUnit> {
    return this.addressFacade.getAddressById(addressId).pipe(SalesOrderUtil.handleNcErrors());
  }

  setSalesOfferState(salesOffer: SdataSalesOffer[]) {
    this.store.dispatch(setSalesOffer({ salesOffer: salesOffer }));
  }

  setOmapiProductsState(omapiProducts: OmapiProduct[]) {
    this.store.dispatch(setOmapiProducts({ omapiProducts: omapiProducts }));
  }
}
