import { AddressUnit, createAddressUtil } from '@sales/shared/address/data-access';
import { InstallationAddressInterface } from '@sales/address/data-access';
import { SalesOrder, ShoppingBasket } from '@sales/goliath/data-access';
import { InternetCheckoutFacade } from '../facade/internet-checkout.facade';
import { forkJoin, mergeMap, Observable, of, pipe, UnaryFunction } from 'rxjs';
import { map } from 'rxjs/operators';
import { CustomerAccountUtil, CustomerProductUtil } from '@sales/internet-checkout/data-access';
import { LanguageEnum } from '@telenet/ng-lib-page';

export class InstallationAddressUtil {
  static loadAndSetCustomerActiveProductAddresses(
    internetCheckoutFacade: InternetCheckoutFacade
  ): UnaryFunction<Observable<SalesOrder>, Observable<boolean | SalesOrder>> {
    return pipe(
      mergeMap((salesOrder) => {
        return internetCheckoutFacade.customerData$.pipe(
          mergeMap((customerData) => {
            return customerData?.customerProducts && customerData.customerAccount
              ? forkJoin(
                  customerData.customerProducts
                    .filter(CustomerProductUtil.isActiveOrSuspended)
                    .filter(CustomerProductUtil.inUniqueLocation)
                    .map((product) =>
                      CustomerAccountUtil.getAddressIdForLocation(customerData.customerAccount, product.locationId)
                    )
                    .filter((addressId): addressId is string => !!addressId) // Filter out undefined/null addressId
                    .map((addressId) => internetCheckoutFacade.getAddressById(addressId))
                )
              : of([]);
          }),
          map((addresses) =>
            InstallationAddressUtil.mapToInstallationAddress(addresses, internetCheckoutFacade.language)
          ),
          map((installationAddresses) => {
            internetCheckoutFacade.setInstallationAddresses(installationAddresses);
            return salesOrder;
          })
        );
      })
    );
  }

  static isRequired(shoppingBasket: ShoppingBasket): boolean {
    return shoppingBasket.omapiProducts.some((offer) => offer.requiresInstallation);
  }

  private static readonly mapToInstallationAddress = (
    installationAddress: AddressUnit[],
    language: LanguageEnum
  ): InstallationAddressInterface[] => {
    const selectedIfOnlyOne = installationAddress.length === 1;
    return installationAddress
      .map((address) => createAddressUtil(address, language))
      .map(
        (addressUtil) =>
          <InstallationAddressInterface>{
            id: addressUtil.getId(),
            street: addressUtil.getStreet(),
            houseNumber: addressUtil.getHouseNumber(),
            postalCode: addressUtil.getPostalCode(),
            municipality: addressUtil.getMunicipality(),
            country: addressUtil.getCountry(),
            isNew: false,
            selected: selectedIfOnlyOne,
            subHouseNumber: addressUtil.getSubHouseNumber(),
            box: addressUtil.getBoxNumber(),
            floor: addressUtil.getFloor(),
          }
      );
  };
}
