import { LogFactory } from '@telenet/ng-lib-shared';
import { AssetService, JsonUrlService, LoaderService, UrlService } from '@telenet/ng-lib-page';
import { ComponentStep, StepFacade } from '@sales/shared/data-access';
import { EventInterface, EventTypeEnum } from '@sales/events/data-access';
import { forkJoin, mergeMap, Observable, OperatorFunction } from 'rxjs';
import { map, take, tap } from 'rxjs/operators';
import { ErrorViewModel } from '@sales/error/data-access';
import { InternetCheckoutConstant } from '../constants/internet-checkout.constant';
import { ErrorObject } from '../interfaces/error.interface';
import { InternetCheckoutFacade } from '../facade/internet-checkout.facade';
import { SdataSalesOffer, ViewStateEnum } from '@sales/internet-checkout/data-access';
import { InternetCheckoutErrorClass } from '../classes/internet-checkout-error.class';
import {
  errorButtonPrefix,
  errorMessagePrefix,
  errorTitlePrefix,
  EventStrategyError,
  GenericError,
} from '../constants/errors.constants';
import { ErrorRedirectUrlEnum } from '../enums/error.enum';
import { Injectable } from '@angular/core';
import { GoliathService, GoliathUtil } from '@sales/goliath/feat-goliath';
import { SalesOrderState, ShoppingBasketEnum, StateAction } from '@sales/goliath/data-access';
import { internetFlow } from '../constants/flow/goliath-flow.constant';
import { defaultStateActions } from '../constants/flow/goliath-state-sequence.constant';
import { ActionProcessor } from '../classes/action-executer';
import { OmapiProduct } from '@telenet/ng-lib-omapi';

@Injectable({
  providedIn: 'root',
})
export class InternetCheckoutService {
  private readonly log = LogFactory.createLogger(InternetCheckoutService);

  constructor(
    private readonly stepFacade: StepFacade,
    private readonly loaderService: LoaderService,
    private readonly internetCheckoutFacade: InternetCheckoutFacade,
    private readonly goliathService: GoliathService,
    private readonly urlService: UrlService,
    private readonly jsonUrlService: JsonUrlService
  ) {}

  executeEventStrategy(): OperatorFunction<[EventInterface | undefined, SalesOrderState], EventInterface | null> {
    // Using the goliath service we need to calculate the delta between the current step and the next one
    // and execute all actions
    return map(([stepEvent]) => {
      if (stepEvent) {
        return stepEvent;
      }
      throw EventStrategyError;
    });
  }

  goToNextStepIfNeeded(event: EventInterface) {
    if (event && event.eventType === EventTypeEnum.STEP_FINISHED) {
      this.stepFacade.nextStepKey$.pipe(take(1)).subscribe({
        next: () => {
          this.loaderService.stop();
          this.stepFacade.goToNextStep();
        },
      });
    }
  }

  logDebug() {
    return tap({
      next: ([stepEvent, salesOrderState]) => {
        this.log.logDebug('Received event: ', {
          stepEvent: stepEvent,
          salesOrderState: salesOrderState,
        });
      },
    });
  }

  initialiseFlow(): Observable<ComponentStep[]> {
    return this.initialiseRequestState().pipe(
      map((omapiProducts) => {
        const flows = [internetFlow];
        const steps = this.goliathService.getSteps([internetFlow], {
          items: [{ type: ShoppingBasketEnum.INTERNET }],
          omapiProducts: omapiProducts,
        });
        const stateActions = this.goliathService.createActionsList(defaultStateActions, [internetFlow.stateActions]);

        this.internetCheckoutFacade.setFlows<InternetCheckoutFacade>(GoliathUtil.removeComponentsFromFlows(flows));
        this.internetCheckoutFacade.setSteps(GoliathUtil.removeComponentsFromSteps(steps));
        this.internetCheckoutFacade.setStateActions<InternetCheckoutFacade>(stateActions);

        this.stepFacade.setStepConfig({ steps: steps.map((step) => step.name.toString()) });
        // Load the first step

        const statesToProcess: SalesOrderState[] = this.goliathService.getStatesToProcess(
          SalesOrderState.INITIALIZED,
          steps.length > 0 ? steps[0].requiredStateBeforeLoad.state : SalesOrderState.INITIALIZED
        );

        const actionsToProcess: StateAction<InternetCheckoutFacade>[] = statesToProcess
          .map((stateToProcess) => {
            return stateActions.find((action) => action.stateProduced === stateToProcess);
          })
          .filter((action) => action !== undefined) as StateAction<InternetCheckoutFacade>[];

        const processor = new ActionProcessor(this.internetCheckoutFacade);
        processor.executeActions(actionsToProcess).subscribe({
          next: (result) => console.log('Action result:', result),
          error: (err) => console.error('Error processing actions:', err),
          complete: () => console.log('All actions processed.'),
        });

        return steps.map((step) => {
          const stepComponent: ComponentStep = {
            key: step.name.toString(),
            component: step.component,
          };
          return stepComponent;
        });
      })
    );
  }

  // store SDATA from URL request parameter and resolve/store omapi information of the requested products
  initialiseRequestState(): Observable<OmapiProduct[]> {
    return this.getSalesOrderRequestFromSdata().pipe(
      tap((salesOffer) => {
        this.internetCheckoutFacade.setSalesOfferState(salesOffer);
      }),
      mergeMap((salesOffer) => {
        const omapiProductRequests = salesOffer.map((offer) => this.internetCheckoutFacade.getOmapiProduct(offer.id));

        return forkJoin(omapiProductRequests).pipe(
          tap((omapiProducts) => this.internetCheckoutFacade.setOmapiProductsState(omapiProducts))
        );
      })
    );
  }

  setOrderCorrection(errorObject: ErrorObject, url: string) {
    const errorViewModel: ErrorViewModel = {
      key: errorObject.key,
      titleKey: errorTitlePrefix.concat(errorObject.titleKey),
      buttonTextKey: errorButtonPrefix.concat(errorObject.buttonTextKey),
      descriptionKey: errorMessagePrefix.concat(errorObject.descriptionKey),
      image: AssetService.getImagePath(
        InternetCheckoutConstant.MODULE_NAME,
        InternetCheckoutConstant.ORDER_CORRECTION_IMAGE
      ),
      mobileImage: AssetService.getImagePath(
        InternetCheckoutConstant.MODULE_NAME,
        InternetCheckoutConstant.ORDER_CORRECTION_MOBILE_IMAGE
      ),
      redirectUrl: url,
    };

    this.internetCheckoutFacade.setViewState({
      key: ViewStateEnum.ERROR,
      showNavigation: false,
      showBackButton: true,
      error: errorViewModel,
    });
  }

  setError(error: Error, customerServiceRedirectUrl: string | undefined, contactUsRedirectUrl: string | undefined) {
    console.log(error.message, error);

    let internetCheckoutError: InternetCheckoutErrorClass;

    if (error instanceof InternetCheckoutErrorClass) {
      internetCheckoutError = error;
    } else {
      internetCheckoutError = new InternetCheckoutErrorClass(GenericError);
    }

    const errorViewModel: ErrorViewModel = {
      key: internetCheckoutError.errorObject.key,
      titleKey: errorTitlePrefix.concat(internetCheckoutError.errorObject.titleKey),
      descriptionKey: errorMessagePrefix.concat(internetCheckoutError.errorObject.descriptionKey),
      buttonTextKey: errorButtonPrefix.concat(internetCheckoutError.errorObject.buttonTextKey),
      redirectUrl: undefined,
      image: AssetService.getImagePath(
        InternetCheckoutConstant.MODULE_NAME,
        InternetCheckoutConstant.ORDER_CORRECTION_IMAGE
      ),
      mobileImage: AssetService.getImagePath(
        InternetCheckoutConstant.MODULE_NAME,
        InternetCheckoutConstant.ORDER_CORRECTION_MOBILE_IMAGE
      ),
    };
    if (customerServiceRedirectUrl && contactUsRedirectUrl) {
      errorViewModel.redirectUrl =
        internetCheckoutError.errorObject.type === ErrorRedirectUrlEnum.CUSTOMER_SERVICE_REDIRECT_URL
          ? customerServiceRedirectUrl
          : contactUsRedirectUrl;
    }
    this.internetCheckoutFacade.setViewState({
      key: ViewStateEnum.ERROR,
      showNavigation: false,
      showBackButton: true,
      error: errorViewModel,
    });
  }

  getSalesOrderRequestFromSdata(): Observable<SdataSalesOffer[]> {
    const encodedString = this.urlService.getRequestParamValue(InternetCheckoutConstant.SDATA, '');

    if (encodedString !== '') {
      return this.jsonUrlService.decompress(encodedString).pipe(
        map((response) => {
          if (response && response['offers'].length > 0) {
            return response['offers'];
          }
        })
      );
    }
    throw new Error('No sales order request found in the URL');
  }
}
