import { Component, Input, OnInit } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ComponentStep, StepsNgrxModule } from '@sales/shared/data-access';
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';
import { SalesStepperComponent } from '@sales/shared/feature-sales-stepper';
import { InternetCheckoutFacade } from './facade/internet-checkout.facade';
import { EventInterface, EventsNgrxModule } from '@sales/events/data-access';
import { combineLatest, Observable } from 'rxjs';
import { LoaderModule, LoaderService } from '@telenet/ng-lib-page';
import { InternetCheckoutService } from './services/internet-checkout.service';
import { StepFlowLibModule } from '@telenet/ng-lib-step-flow';
import { tap } from 'rxjs/operators';
import { ViewState, ViewStateEnum } from '@sales/hardware-checkout/data-access';
import { ErrorComponent } from '@sales/error/feat-error';
import { SalesInternetCheckoutNgrxModule } from '@sales/internet-checkout/data-access';
import { AddressNgrxModule } from '@sales/address/data-access';

@Component({
  standalone: true,
  selector: 'tg-sales-internet-checkout',
  templateUrl: './internet-checkout.component.html',
  imports: [
    CommonModule,
    StepsNgrxModule,
    SalesStepperComponent,
    StepFlowLibModule,
    LoaderModule,
    ErrorComponent,
    SalesInternetCheckoutNgrxModule,
    AddressNgrxModule,
    EventsNgrxModule,
  ],
})
@UntilDestroy()
export class InternetCheckoutComponent implements OnInit {
  @Input() enableMask: boolean;
  @Input() enableLoader = true;
  @Input() pageType: string;
  @Input() customerServiceRedirectUrlInput: string | undefined;
  @Input() contactUsRedirectUrlInput: string | undefined;
  @Input() thankyouRedirectUrl: string | undefined;
  @Input() termsAndConditionsUrl: string | undefined;

  componentSteps: ComponentStep[] = [];
  isLoaded = false;

  viewState$: Observable<ViewState>;
  viewStateEnum = ViewStateEnum;

  constructor(
    private readonly loaderService: LoaderService,
    private readonly internetCheckoutFacade: InternetCheckoutFacade,
    private readonly internetCheckoutService: InternetCheckoutService
  ) {}

  ngOnInit(): void {
    this.loaderService.start();

    combineLatest([this.internetCheckoutFacade.stepEvent$, this.internetCheckoutFacade.salesOrderState$])
      .pipe(
        this.internetCheckoutService.logDebug(),
        this.internetCheckoutService.executeEventStrategy(),
        untilDestroyed(this)
      )
      .subscribe({
        next: (event: EventInterface | null) => {
          this.loaderService.stop();
          if (event) {
            this.internetCheckoutService.goToNextStepIfNeeded(event);
          }
        },
        error: (error) => {
          this.handleError(error);
        },
      });

    this.initialiseFlow();
  }

  nextButtonClickedEvent() {
    console.log('next button clicked');
  }

  private initialiseFlow() {
    try {
      this.internetCheckoutService
        .initialiseFlow()
        .pipe(tap((componentSteps: ComponentStep[]) => (this.componentSteps = componentSteps)))
        .pipe(untilDestroyed(this))
        .subscribe({
          next: () => {
            this.isLoaded = true;
            this.loaderService.stop();
          },
          error: (error) => {
            this.handleError(error);
          },
        });
    } catch (error) {
      this.handleError(error as Error);
    }
  }

  private handleError(error: Error) {
    this.loaderService.stop();
    this.internetCheckoutService.setError(error, this.customerServiceRedirectUrlInput, this.contactUsRedirectUrlInput);
  }

  protected readonly ViewStateEnum = ViewStateEnum;
}
