import { from, of, Observable } from 'rxjs';
import { catchError, concatMap } from 'rxjs/operators';
import { StateAction } from '@sales/goliath/data-access';
import { InternetCheckoutFacade } from '../facade/internet-checkout.facade';

export class ActionProcessor {
  constructor(private readonly internetCheckoutFacade: InternetCheckoutFacade) {}

  executeActions(stateActions: StateAction<InternetCheckoutFacade>[]): Observable<unknown> {
    // Changed return type to any for general use
    return from(stateActions).pipe(concatMap((action) => this.executeSingleAction(action)));
  }

  private executeSingleAction(action: StateAction<InternetCheckoutFacade>): Observable<unknown> {
    // Handling the execution of each action
    if (action.executor) {
      // Call the executor with the service and wrap it in an observable context if not already an observable
      const operation = action.executor(this.internetCheckoutFacade); // This should be a UnaryFunction<Observable<any>, Observable<any>>
      return this.internetCheckoutFacade.salesOrder$.pipe(
        operation, // Apply the operation provided by the executor
        catchError((error) => {
          console.error(`Error executing action for ${action.name}:`, error);
          return of(`Error in action: ${action.name}`); // Continue stream with error info or a default value on error
        })
      );
    } else {
      console.warn(`No executor defined for action: ${action.name}`);
      return of(`No executor for: ${action.name}`); // Return info if no executor
    }
  }
}
