import { internetStateActions } from './goliath-state-sequence.constant';
import { DeliveryComponent } from '@sales/delivery/feat-delivery';
import { SummaryStepComponent } from '@sales/summary/feat-summary';
import { Flow, SalesOrderState, ShoppingBasket, ShoppingBasketEnum, StepNameEnum } from '@sales/goliath/data-access';
import { EasySwitchComponent } from '@sales/easy-switch/feat-easy-switch';
import { AppointmentComponent } from '@sales/appointment/feat-appointment';
import { InstallationTypeComponent } from '@sales/installation-type/feat-installation-type';
import { ThankYouStepComponent } from '@sales/thank-you/feat-thank-you';
import { InternetCheckoutFacade } from '../../facade/internet-checkout.facade';
import { FeatInstallationAddressComponent } from '@sales/address/feat-installation-address';
import { InstallationAddressUtil } from '../../utils/installation-address.util';

export const internetFlow: Flow<InternetCheckoutFacade> = {
  name: 'Internet flow',
  isRequired: (shoppingBasket: ShoppingBasket) =>
    shoppingBasket.items.some((item) => item.type === ShoppingBasketEnum.INTERNET),
  steps: [
    {
      name: StepNameEnum.INSTALLATION_ADDRESS,
      component: FeatInstallationAddressComponent,
      requiredStateBeforeLoad: {
        state: SalesOrderState.ORDER_ITEMS_ADDED,
      },
      isRequired: (shoppingBasket) => InstallationAddressUtil.isRequired(shoppingBasket),
    },
    {
      name: StepNameEnum.INSTALLATION_TYPE,
      component: InstallationTypeComponent,
      requiredStateBeforeLoad: {
        state: SalesOrderState.ORDER_ITEMS_ADDED,
      },
      isRequired: () => true,
    },
    {
      name: StepNameEnum.APPOINTMENT,
      component: AppointmentComponent,
      requiredStateBeforeLoad: {
        state: SalesOrderState.INSTALLATION_CONFIGURED,
      },
      isRequired: () => true,
    },
    {
      name: StepNameEnum.DELIVERY,
      component: DeliveryComponent,
      requiredStateBeforeLoad: {
        state: SalesOrderState.INSTALLATION_CONFIGURED,
      },
      isRequired: () => true,
    },
    {
      name: StepNameEnum.EASY_SWITCH,
      component: EasySwitchComponent,
      requiredStateBeforeLoad: {
        state: SalesOrderState.INSTALLATION_CONFIGURED,
      },
      isRequired: () => true,
    },
    {
      name: StepNameEnum.SUMMARY,
      component: SummaryStepComponent,
      requiredStateBeforeLoad: {
        state: SalesOrderState.APPOINTMENT_CONFIGURED,
      },
      isRequired: () => true,
    },
    {
      name: StepNameEnum.CONFIRMATION,
      component: ThankYouStepComponent,
      requiredStateBeforeLoad: {
        state: SalesOrderState.APPOINTMENT_CONFIGURED,
      },
      isRequired: () => true,
    },
  ],
  stateActions: internetStateActions,
};
