import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ConfigConstants, ConfigService } from '@telenet/ng-lib-config';
import { Observable } from 'rxjs';
import { HeaderUtil } from '../../hardware-checkout/utils/header.util';
import { UrlService } from '@telenet/ng-lib-page';
import { CustomerAccount, CustomerProduct } from '@sales/internet-checkout/data-access';

@Injectable({ providedIn: 'root' })
export class ReverseProxyService {
  baseUrl: string;
  basePath = '/public/api/mytelenet-web-sales-checkout-rp';

  businessDomainPlan = 'Mobile Plan,Internet,TV,Fixed Phone,Bundle,Streaming Services Container';

  constructor(
    private readonly http: HttpClient,
    private readonly urlService: UrlService,
    private readonly configService: ConfigService
  ) {
    this.baseUrl = this.configService.getConfig(ConfigConstants.OCAPI_URl);
  }

  getCustomerProducts(): Observable<CustomerProduct[]> {
    const path = this.urlService.buildUrlWithParams(
      '/v1/orderManagement/customerAccount/customerProduct',
      new Map([['businessDomains', this.businessDomainPlan]])
    );
    return this.doGet<CustomerProduct[]>(this.getUrl(path));
  }

  getCustomerAccount(): Observable<CustomerAccount> {
    const path = '/v1/customerManagement/customerAccount';
    return this.doGet<CustomerAccount>(this.getUrl(path));
  }

  private getUrl(path: string) {
    return `${this.baseUrl}${this.basePath}${path}`;
  }

  private doGet<T>(url: string): Observable<T> {
    return this.http.get<T>(url, {
      headers: HeaderUtil.createHttpHeaders(this.urlService.getCurrentUrl()),
      withCredentials: true,
    });
  }
}
